*, :before, :after {
  box-sizing: border-box;
}

body {
  background: #3b4465;
  margin: 0;
  font-family: Roboto, -apple-system, Helvetica Neue, Segoe UI, Arial, sans-serif;
}

.forms-section {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.section-title {
  letter-spacing: 1px;
  color: #fff;
  font-size: 32px;
}

.forms {
  align-items: flex-start;
  margin-top: 30px;
  display: flex;
}

.form-wrapper {
  animation: .3s ease-out forwards hideLayer;
}

.form-wrapper.is-active {
  animation: .3s ease-in forwards showLayer;
}

@keyframes showLayer {
  50% {
    z-index: 1;
  }

  100% {
    z-index: 1;
  }
}

@keyframes hideLayer {
  0% {
    z-index: 1;
  }

  49.999% {
    z-index: 1;
  }
}

.switcher {
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: .5px;
  color: #999;
  background-color: #0000;
  border: none;
  outline: none;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  font-family: inherit;
  font-size: 16px;
  transition: all .3s ease-out;
  display: block;
  position: relative;
  transform: translateX(0);
}

.form-wrapper.is-active .switcher-login {
  color: #fff;
  transform: translateX(90px);
}

.form-wrapper.is-active .switcher-signup {
  color: #fff;
  transform: translateX(-90px);
}

.underline {
  pointer-events: none;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: -5px;
  left: 0;
  overflow: hidden;
}

.underline:before {
  content: "";
  top: 0;
  left: inherit;
  width: inherit;
  height: inherit;
  background-color: currentColor;
  transition: transform .2s ease-out;
  display: block;
  position: absolute;
}

.switcher-login .underline:before {
  transform: translateX(101%);
}

.switcher-signup .underline:before {
  transform: translateX(-101%);
}

.form-wrapper.is-active .underline:before {
  transform: translateX(0);
}

.form {
  transform-origin: top;
  border-radius: 5px;
  min-width: 260px;
  margin-top: 50px;
  padding: 30px 25px;
  overflow: hidden;
}

.form-login {
  animation: .3s ease-out forwards hideLogin;
}

.form-wrapper.is-active .form-login {
  animation: .3s ease-in forwards showLogin;
}

@keyframes showLogin {
  0% {
    background: #d7e7f1;
    transform: translate(40%, 10px);
  }

  50% {
    transform: translate(0);
  }

  100% {
    background-color: #fff;
    transform: translate(35%, -20px);
  }
}

@keyframes hideLogin {
  0% {
    background-color: #fff;
    transform: translate(35%, -20px);
  }

  50% {
    transform: translate(0);
  }

  100% {
    background: #d7e7f1;
    transform: translate(40%, 10px);
  }
}

.form-signup {
  animation: .3s ease-out forwards hideSignup;
}

.form-wrapper.is-active .form-signup {
  animation: .3s ease-in forwards showSignup;
}

@keyframes showSignup {
  0% {
    background: #d7e7f1;
    transform: translate(-40%, 10px)scaleY(.8);
  }

  50% {
    transform: translate(0)scaleY(.8);
  }

  100% {
    background-color: #fff;
    transform: translate(-35%, -20px)scaleY(1);
  }
}

@keyframes hideSignup {
  0% {
    background-color: #fff;
    transform: translate(-35%, -20px)scaleY(1);
  }

  50% {
    transform: translate(0)scaleY(.8);
  }

  100% {
    background: #d7e7f1;
    transform: translate(-40%, 10px)scaleY(.8);
  }
}

.form fieldset {
  opacity: 0;
  border: 0;
  margin: 0;
  padding: 0;
  transition: all .3s ease-out;
  position: relative;
}

.form-login fieldset {
  transform: translateX(-50%);
}

.form-signup fieldset {
  transform: translateX(50%);
}

.form-wrapper.is-active fieldset {
  opacity: 1;
  transition: opacity .4s ease-in, transform .35s ease-in;
  transform: translateX(0);
}

.form legend {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  position: absolute;
  overflow: hidden;
}

.input-block {
  margin-bottom: 20px;
}

.input-block label {
  color: #a1b4b4;
  font-size: 14px;
}

.input-block input {
  color: #3b4465;
  background: #eef9fe;
  border: 1px solid #cddbef;
  border-radius: 2px;
  width: 100%;
  margin-top: 8px;
  padding-left: 15px;
  padding-right: 15px;
  font-size: 16px;
  line-height: 40px;
  display: block;
}

.form [type="submit"] {
  opacity: 0;
  border: none;
  border-radius: 25px;
  min-width: 120px;
  margin: 30px auto 10px;
  font-size: 18px;
  line-height: 40px;
  transition: all .3s ease-out;
  display: block;
}

.form-wrapper.is-active .form [type="submit"] {
  opacity: 1;
  transition: all .4s ease-in;
  transform: translateX(0);
}

.btn-login {
  color: #fbfdff;
  background: #a7e245;
  transform: translateX(-30%);
}

.btn-signup {
  color: #a7e245;
  background: #fbfdff;
  transform: translateX(30%);
  box-shadow: inset 0 0 0 2px #a7e245;
}

